"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Check, Loader2 } from "lucide-react"
import { useAuth } from "@/lib/auth-context"
import useSWR from "swr"

interface Plan {
  _id: string
  name: string
  price: number
  credits: number
  features: string[]
  isPopular?: boolean
}

const fetcher = (url: string) => fetch(url).then((res) => res.json())

export function PricingSection() {
  const { user } = useAuth()
  const { data, error, isLoading } = useSWR<{ plans: Plan[] }>("/api/plans", fetcher)

  const plans = data?.plans || []

  return (
    <section className="py-20" id="pricing">
      <div className="mx-auto max-w-7xl px-6">
        <div className="mx-auto max-w-2xl text-center">
          <div className="mb-4 inline-flex items-center gap-2 rounded-full border border-border bg-secondary/50 px-4 py-1.5">
            <span className="text-xs font-medium text-primary">New</span>
            <span className="text-xs text-muted-foreground">Credit-Based Pricing Available</span>
          </div>
          <h2 className="text-balance text-3xl font-bold text-foreground sm:text-4xl">Plans and Pricing</h2>
          <p className="mt-4 text-pretty text-muted-foreground">
            Get started immediately for free. Upgrade for more conversions and API access.
          </p>
        </div>

        {isLoading ? (
          <div className="mt-12 flex justify-center">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
          </div>
        ) : error ? (
          <div className="mt-12 text-center text-red-500">
            Failed to load plans. Please try again later.
          </div>
        ) : (
          <div className="mt-12 grid gap-6 lg:grid-cols-3">
            {plans.map((plan) => (
              <div
                key={plan._id}
                className={`relative rounded-xl border p-6 transition-all hover:shadow-lg ${
                  plan.isPopular ? "border-primary bg-card shadow-md" : "border-border bg-card"
                }`}
              >
                {plan.isPopular && (
                  <div className="absolute -top-3 left-6">
                    <span className="rounded-full bg-primary px-3 py-1 text-xs font-medium text-primary-foreground">
                      Recommended
                    </span>
                  </div>
                )}

                <div className="mb-6">
                  <h3 className="text-lg font-semibold text-foreground capitalize">{plan.name}</h3>
                  <div className="mt-2 flex items-baseline">
                    <span className="text-4xl font-bold text-foreground">${plan.price}</span>
                    <span className="text-muted-foreground">/month</span>
                  </div>
                  <p className="mt-2 text-sm text-muted-foreground">
                    {plan.credits} credits included
                  </p>
                </div>

                <ul className="mb-6 space-y-3">
                  {plan.features.map((feature, i) => (
                    <li key={i} className="flex items-center gap-2 text-sm text-muted-foreground">
                      <Check className="h-4 w-4 text-primary" />
                      {feature}
                    </li>
                  ))}
                </ul>

                <Link href={user ? "/dashboard/billing" : "/register"}>
                  <Button className="w-full" variant={plan.isPopular ? "default" : "outline"}>
                    {user ? "Upgrade" : "Get Started"}
                  </Button>
                </Link>
              </div>
            ))}
          </div>
        )}
      </div>
    </section>
  )
}
