"use client"

import Link from "next/link"
import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { FileText, Menu, X } from "lucide-react"
import { useAuth } from "@/lib/auth-context"
import { cn } from "@/lib/utils"

export function Navbar() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const [scrolled, setScrolled] = useState(false)
  const { user } = useAuth()

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 20)
    }
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <header 
      className={cn(
        "fixed top-0 left-0 right-0 z-50 transition-all duration-300",
        scrolled ? "bg-white/90 backdrop-blur-md shadow-sm py-3" : "bg-transparent py-5"
      )}
    >
      <nav className="mx-auto flex max-w-7xl items-center justify-between px-6">
        <Link href="/" className="flex items-center gap-2 group">
          <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-[#0f393b] text-white transition-transform group-hover:scale-105">
            <FileText className="h-5 w-5" />
          </div>
          <span className="text-xl font-serif font-bold tracking-tight text-[#0f393b]">PDFForge</span>
        </Link>

        <div className="hidden items-center gap-8 md:flex">
          <Link href="/" className="text-[15px] font-medium text-[#0f393b]/70 transition-colors hover:text-[#0f393b]">
            Platform
          </Link>
          <Link href="/pricing" className="text-[15px] font-medium text-[#0f393b]/70 transition-colors hover:text-[#0f393b]">
            Pricing
          </Link>
          <Link href="/docs" className="text-[15px] font-medium text-[#0f393b]/70 transition-colors hover:text-[#0f393b]">
            Resources
          </Link>
        </div>

        <div className="hidden items-center gap-4 md:flex">
          {user ? (
            <Link href="/dashboard">
              <Button size="sm" className="rounded-full bg-[#0f393b] px-6 font-medium text-white hover:bg-[#0f393b]/90">Dashboard</Button>
            </Link>
          ) : (
            <>
              <Link href="/login">
                <Button variant="ghost" size="sm" className="font-medium text-[#0f393b] hover:bg-[#0f393b]/5 hover:text-[#0f393b]">
                  Sign in
                </Button>
              </Link>
              <Link href="/register">
                <Button size="sm" className="rounded-full bg-[#008060] px-6 font-medium text-white shadow-md shadow-[#008060]/20 transition-transform hover:scale-105 hover:bg-[#006b50]">Get Started</Button>
              </Link>
            </>
          )}
        </div>

        <button className="md:hidden" onClick={() => setMobileMenuOpen(!mobileMenuOpen)}>
          {mobileMenuOpen ? <X className="h-6 w-6 text-[#0f393b]" /> : <Menu className="h-6 w-6 text-[#0f393b]" />}
        </button>
      </nav>

      {mobileMenuOpen && (
        <div className="absolute top-full left-0 right-0 border-t border-gray-100 bg-white px-6 py-6 shadow-xl md:hidden animate-in slide-in-from-top-5">
          <div className="flex flex-col gap-4">
            <Link href="/" className="text-base font-medium text-[#0f393b]/80 hover:text-[#0f393b]" onClick={() => setMobileMenuOpen(false)}>
              Platform
            </Link>
            <Link href="/pricing" className="text-base font-medium text-[#0f393b]/80 hover:text-[#0f393b]" onClick={() => setMobileMenuOpen(false)}>
              Pricing
            </Link>
            <Link href="/docs" className="text-base font-medium text-[#0f393b]/80 hover:text-[#0f393b]" onClick={() => setMobileMenuOpen(false)}>
              Resources
            </Link>
            <div className="flex flex-col gap-3 pt-4 border-t border-gray-100">
              {user ? (
                <Link href="/dashboard" onClick={() => setMobileMenuOpen(false)}>
                  <Button className="w-full rounded-full bg-[#0f393b] text-white">Dashboard</Button>
                </Link>
              ) : (
                <>
                  <Link href="/login" onClick={() => setMobileMenuOpen(false)}>
                    <Button variant="ghost" className="w-full text-[#0f393b]">
                      Sign in
                    </Button>
                  </Link>
                  <Link href="/register" onClick={() => setMobileMenuOpen(false)}>
                    <Button className="w-full rounded-full bg-[#008060] text-white">Get Started</Button>
                  </Link>
                </>
              )}
            </div>
          </div>
        </div>
      )}
    </header>
  )
}
